@keyframes showWindow {
    from {
        transform: scale(0);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
  }

  @keyframes showPart {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
  }

  @keyframes movePart {
    0%{
        transform: translate(1px);
    }
    50%{
        transform: translate(0px);
    }
    100%{
        transform: translate(-1px);
    }
    
  }

  @keyframes showModal {
    from {
        top: -100%;
    }
    to {
        top: 0%;
    }
  }

  